.navbar {
  --bs-navbar-color: rgba(var(--bs-dark-rgb), 0.9);
  --bs-navbar-hover-color: rgba(var(--bs-dark-rgb), 1);
  --bs-navbar-disabled-color: rgba(var(--bs-dark-rgb), 0.7);
  --bs-navbar-active-color: rgba(var(--bs-teal-rgb), 1);
}

.navbar-dark,
.navbar[data-bs-theme=dark] {
  --bs-navbar-color: rgba(var(--bs-white-rgb), 0.9);
  --bs-navbar-hover-color: rgba(var(--bs-white-rgb), 1);
  --bs-navbar-disabled-color: rgba(var(--bs-white-rgb), 0.7);
  --bs-navbar-active-color: rgba(var(--bs-white-rgb), 1);
}

.nav-link {
  position: relative;
}

.dropdown-menu {
  box-shadow: var(--bs-dropdown-box-shadow);
}

.container,
.container-fluid,
.container-xxl,
.container-xl,
.container-lg,
.container-md,
.container-sm {
  --bs-gutter-x: 4rem;
}

.material-symbols-rounded {
  font-variation-settings: "FILL" 1;
}

.material-symbols-rounded.icon-outline {
  font-variation-settings: "FILL" 0;
}